footer {
  display: flex;
  justify-content: center;
  background-color: #18191a;
  color: white;
}

.wrapper {
  padding: 20px 10px 10px;
  flex-direction: column;
}

.socials {
  display: flex;
  justify-content: center;
  gap: 30px;

  @media screen and (max-width: 480px) {
    gap: 15px;
  }


  & a {
    color: white;
    text-decoration: none;
  }
}

